#profile 
{
  position: relative;
  width: 100%;
  height: 100vh;
  background-color: black;
  display: grid;
  grid-template-columns: repeat(2,1fr);
  align-items: center;
  justify-items: center;
}

.profileItems
{
  width: 50%;
  height: 70%;
  background-color:white;
  border-radius:10px;
  border: none;
  display: flex;
  flex-direction: column;
  justify-content:center;
  align-items:center;
  box-shadow: 0 0 10px white;
}

.profileItems h3
{
  width: 100%;
  height: 20%;
  line-height: 58px;
  text-align: center;
  font-weight: 800;
}

.profileItems span
{
  width: 100%;
  height: 80%;
  padding: 30px;
  font-size: 20px;
  font-weight: 600;
}

.profileItems span p
{
  margin-bottom: 20px;
}

.profileItems span p a
{
  color: inherit;
}

#profile > a
{
  position: absolute;
  text-align: center;
  line-height: 46px;
  left: 44%;
  top: 48%;
  width: 12%;
  height: 5%;
  color: white;
  background-color:green;
  border-radius: 10px;
  box-shadow:0 0 10px green;
  font-weight: 700;
}

#profile > a:active
{
  background-color:white;
  color: green;
  box-shadow: 0 0 10px white;
}
