*
{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style-type: none;
}


#allWrap
{
  position: relative;
  width: 100%;
  max-width: 100%;
  min-height: 100vh; /* âˆµ vw scroll bar issue*/
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

#app
{
  position: relative;
  display: flex;
  justify-content:center;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  background: rgb(33, 33, 33);
  overflow: hidden;
}

#mainIntro
{
  display: flex;
  flex-direction: column;
  justify-content:center;
  align-items:center;
  position: relative;
  right: 100px;
}

img
{
  width: 150px;
  height: 200px;
  box-shadow: 0 0 10px white;
  border: none;
  border-radius: 10px;
}

#mainIntro span
{
  margin-top: 50px;
  width: 400px;
  height: 150px;
  border-bottom: 1px solid white;
  box-shadow: 0 0 10px white;
  border-radius: 10px;
  text-align: center;
  color: white;
  font-size: 30px;
  line-height: 47px;
  font-weight: 600;
}

.backPhrase1
{
  position: absolute;
  top: 150px;
  left: 50px;
  transform: rotate(-45deg);
  font-size: 50px;
  font-weight: 900;
  animation: glowing 5s linear infinite;
}

.backPhrase2
{
  position: absolute;
  top: 140px;
  right: 100px;
  transform: rotate(30deg);
  font-size: 50px;
  font-weight: 900;
  animation: glowing2 5s ease-in infinite;
}

.backPhrase3
{
  position: absolute;
  bottom: 100px;
  left: 50%;
  transform: rotate(-20deg);
  font-size: 50px;
  font-weight: 900;
  animation: glowing 5s cubic-bezier(0.175, 0.705, 0.12, 1.275) infinite;
}

.backPhrase4
{
  position: absolute;
  right: 50px;
  font-size: 50px;
  font-weight: 900;
  animation: glowing2 5s cubic-bezier(0.175, 0.885, 0.32, 1.275) infinite;
}

.backPhrase5
{
  position: absolute;
  left: 50px;
  bottom: 30px;
  font-size: 50px;
  font-weight: 900;
  animation: glowing 5s ease-out infinite;
}

.backPhrase6
{
  position: absolute;
  right: 30px;
  bottom: 30px;
  font-size: 50px;
  font-weight: 900;
  animation: glowing2 5s linear infinite;
}

.backPhrase7
{
  position: absolute;
  top: 30px;
  font-size: 50px;
  font-weight: 900;
  animation: glowing 5s ease-in-out infinite;
}

@keyframes glowing
{
  0%
  {
    color: red;
    text-shadow: 0 0 10px rgb(178, 41, 41);
    filter: hue-rotate(30deg);
  }
  25%
  {
    color: blue;
    text-shadow: 0 10px 0 rgb(33, 33, 152);
    filter: hue-rotate(30deg);
  }
  50%
  {
    color: green;
    text-shadow: -10px 0 0 rgb(27, 149, 27);
    filter: hue-rotate(30deg);
  }
  75%
  {
    color: yellow;
    text-shadow: 0 -10px 0 rgb(189, 189, 28);
    filter: hue-rotate(30deg);
  }
  100%
  {
    color: white;
    text-shadow: 0 0 -10px white;
    filter: hue-rotate(30deg);
  }
}

@keyframes glowing2
{
  0%
  {
    color: purple;
    text-shadow: 0 0 -10px rgb(126, 16, 126);
    filter: hue-rotate(30deg);
  }
  25%
  {
    color: gray;
    text-shadow: -10px 0 0 rgb(201, 190, 190);
    filter: hue-rotate(30deg);
  }
  50%
  {
    color: blueviolet;
    text-shadow: 0 10px 0 rgb(88, 36, 137);
    filter: hue-rotate(30deg);
  }
  75%
  {
    color: yellowgreen;
    text-shadow: 10px 0 0 yellow;
    filter: hue-rotate(30deg);
  }
  100%
  {
    color: white;
    text-shadow: 0 0 10px white;
    filter: hue-rotate(30deg);
  }
}