#repository
{
  width: 100%;
  height: 100vh;
  background-color:rgb(8, 8, 8);
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

#repository .list
{
  width: 20%;
  height: 80%;
  margin: 0 auto;
  background-color: white;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content:center;
  align-items: center;
  box-shadow:  0 0 10px white;
}

.list h3 
{
  width: 90%;
  height: 5%;
  margin-bottom: 10px;
  text-align: center;
  line-height: 37px;
}

.list img 
{
  width: 90%;
  height: 30%;
  border-radius: 10px;
  margin-bottom: 10px;
}

.list span 
{
  position: relative;
  width: 90%;
  height: 55%;
  border: none;
  box-shadow: 0 0 3px black;
  border-radius: 10px;
  padding: 10px;
  font-weight: 700;
}

.list span > a 
{
  position: absolute;
  display: inline-block;
  width: 30%;
  height: 10%;
  background: green;
  border-radius: 30px;
  text-align: center;
  line-height: 39px;
  color: white;
  bottom: 10%;
  left: 35%;
  box-shadow: 0 0 5px green;
}

.list span > a:hover
{
  color: yellow;
}

.list span > a:active
{
  color: green;
  background-color:yellow;
  box-shadow: 0 0 5px yellow;
}




