#skills
{
  position:relative;
  width: 100%;
  height: 100vh;
  background-color: rgb(27, 26, 26);
}

#skills > div
{
  text-align: center;
  border-bottom: 1px solid white;
  border-radius: 10px;
  border-top-right-radius: 10px;
  position: absolute;
  background-color:rgba(255,255,255,0.1);
  box-shadow: 0 0 10px white;
} 

#skills div h2
{
  width: 100%;
  height: 15%;
  color: white;
  line-height:37px;
}

#skills div .iconWrap
{
  width: 100%;
  height: 85%;
  display: flex;
  justify-content:space-around;
  align-items:center;
}


#front
{
  top: 10px;
  left: 10px;
  width: 23%;
  height: 40%;
}

#skills #front .iconWrap
{
  display: grid;
  grid-template-columns: repeat(3,1fr);
  justify-items: center;
}

#back
{
  top: 25%;
  left: 25%;
  width: 23%;
  height: 40%;
}

#DB
{
  bottom: 25%;
  right: 25%;
  width: 23%;
  height: 40%;
}

#tool
{
  bottom: 10px;
  right: 10px;
  width: 23%;
  height: 40%;
}

#skills .icon
{
  width: 100px;
  height: 100px;
  position: relative;
  border: none;
  box-shadow: none;
}


.iconWrap img:hover 
{
  cursor: pointer;
  transform: scale(1.2);
}

.skillTitle
{
  font-size: 100px;
  font-weight: bold;
  position: absolute;
  top: 50px;
  right: 50px;
  color: white;
  text-shadow: 0 0 10px white;
  animation: colorChange 5s linear infinite;
}

.skillDescription
{
  font-size: 100px;
  font-weight: bold;
  position: absolute;
  bottom: 50px;
  left: 50px;
  color: white;
  text-shadow: 0 0 10px white;
  animation: colorChange 5s linear infinite;
}

@keyframes colorChange {
  0% { 
    color: white;
    text-shadow: 0 0 10px white;
  }
  20% {
    color: rgb(64, 255, 0);
    text-shadow: 0 0 10px rgb(64, 255, 0);
  }  
  40% {
    color: rgb(11, 240, 213);
    text-shadow: 0 0 10px rgb(11, 240, 213);
  }
  60% {
    color: rgb(34, 59, 225);
    text-shadow: 0 0 10px rgb(34, 59, 225);
  }  
  80% {
    color: rgb(166, 22, 206);
    text-shadow: 0 0 10px rgb(166, 22, 206);
  }  
  100% {
    color: white;
    text-shadow: 0 0 10px white;
  }    
}




