:root
{
  --minus:-1;
  --zero:0;
  --one:1;
  --two:2;
  --three:3;
}

#wrap
{
  position:relative;
  left: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 600px;
  min-height: 800px;
  overflow: hidden;
}

