.cubeContainer
{
  position: relative;
  top: -110px;
  transform: skewY(-20deg);
  animation: animate 5s linear infinite;
}

@keyframes animate 
{
  0%
  {
    filter: hue-rotate(0deg);
  }
  100%
  {
    filter: hue-rotate(360deg)
  }
}

.cubeContainer .cube
{
  position: relative;
  z-index: 2;
}

.cubeContainer .cube:nth-child(2)
{
  z-index: 1;
  translate: -60px -60px;
}

.cubeContainer .cube:nth-child(3)
{
  z-index: 3;
  translate: 60px 60px;
}

.cubeContainer .cube div
{
  position: absolute;
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.cubeContainer .cube div:first-child
{
  translate: calc(-70px * var(--minus)) calc(-60px * var(--zero));
}
.cubeContainer .cube div:nth-child(2)
{
  translate: calc(-70px * var(--zero)) calc(-60px * var(--zero));
}
.cubeContainer .cube div:last-child
{
  translate: calc(-70px * var(--one)) calc(-60px * var(--zero));
}


.cubeContainer .cube div span
{
  position: relative;
  display: inline-block;
  width: 50px;
  height: 50px;
  background-color: rgb(119, 116, 116);
  box-shadow: 0 0 30px rgb(119, 116, 116);
  transition:1.5s;
}

.cubeContainer .cube span:first-child
{
  z-index: 3;
}

.cubeContainer .cube span:nth-child(2)
{
  z-index: 2;
}

.cubeContainer .cube span:last-child
{
  z-index: 1;
}


.cubeContainer .cube div span:hover
{
  transition: 0s;
  background-color:rgb(221, 64, 64);
  filter: drop-shadow(0 0 30px rgb(221, 64, 64));
}

.cubeContainer .cube div span::before
{
  content:'';
  position: absolute;
  left: -40px;
  width: 40px;
  height: 100%;
  background-color:rgb(145, 141, 141);
  transform-origin: right;
  transform: skewY(45deg);
  box-shadow: 0 0 30px rgb(145, 141, 141);
  transition: 1.5s;
}

.cubeContainer .cube div span:hover::before
{
  transition: 0s;
  background-color:rgb(221, 64, 64);
  filter: drop-shadow(0 0 30px rgb(221, 64, 64));
}

.cubeContainer .cube div span::after
{
  content:'';
  position: absolute;
  top: -40px;
  left: 0;
  width: 100%;
  height: 40px;
  background-color:#aba7a7;
  transform-origin: bottom;
  transform: skewX(45deg);
  transition: 1.5s;
  box-shadow: 0 0 30px #aba7a7;
}

.cubeContainer .cube div span:hover::after
{
  transition: 0s;
  background-color:rgb(221, 64, 64);
  filter: drop-shadow(0 0 30px rgb(221, 64, 64));
}