#contact
{
  width: 100%;
  height: 100vh;
  background-color:rgb(37, 36, 36);
  display: flex;
  justify-content:center;
  align-items:center;
}

#contact form
{
  width: 70%;
  height: 80%;
  border: 1px solid rgb(151, 145, 145);
  border-radius: 10px;
  box-shadow: 0 0 10px white;
  display: flex;
  justify-content:space-around;
  align-items:center;
  font-weight: 700;
  font-size: 30px;
  text-shadow: 0 0 10px white;
  color: white;
}

#contact form #info
{
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content:center;
}

#contact form #info h2
{
  position: relative;
  color: white;
  text-shadow: 0 0 10px white;
  font-weight: 700;
  bottom: 15%;
}

#contact form #info input
{
  background-color:transparent;
  border: none;
  border-bottom: 1px solid rgb(151, 145, 145);
  height: 5%;
  width: 50%;
  color: white;
  margin: 30px 0;
  font-size: 20px;
}

#contact form #message
{
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content:space-around;
}

#contact form textarea
{
  width: 90%;
  height: 70%;
  resize: none;
  border: none;
  background-color:transparent;
  border: 1px solid white;
  font-size: 20px;
  padding: 20px;
  color: white;
  font-weight: 1000;
}

#contact #message input:last-child
{
  position: relative;
  left: 470px;
  width: 20%;
  height: 5%;
  color: white;
  border: none;
  background-color:rgb(35, 182, 35);
  cursor:pointer;
  border-radius: 10px;
  font-size: 20px;
  font-weight: 700; 
}

#contact #message input:last-child:active
{
  transition: 0.5s;
  animation: clicked 1s linear;
}

@keyframes clicked
{
  0%{background: white;color: rgb(35, 182, 35);box-shadow: 0 0 10px rgb(35, 182, 35);}
  100% { background: transparent;color: white;}
}

#contact form input:not(#contact form #message input:last-child):focus,textarea:focus
{
  outline: none;
  box-shadow: 0 2px 5px white;
}

